html, body {
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item:first-child {
  margin-left: 0;
}
